  @import url('https://fonts.googleapis.com/css2?family=Alkatra:wght@500&family=Roboto+Condensed:wght@300;400;700&display=swap');

.background{
  position: fixed;
  top: -10;
  left: -10;
  width: 130%;
  height: 130%;
  z-index: -1; 
  background-image: url(/src/images/background.png);
  background-size: cover;
  filter: blur(40px) opacity(50%);
 
}
body{
  background-color: #FFF;
   height: 100vh !important;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  
}

a{
  color: white;
  text-decoration: none;
  text-shadow: 0px 0px 10px #9e0c0c;
}

/**************************************************** p√°ginas ****************************************************/

.card {
  min-height: 500px;
  border: none;
  padding: 2rem;
  background: rgb(255, 81, 81);
  background: linear-gradient(360deg, rgba(255, 81, 81, 1) 0%, rgba(254, 147, 147, 1) 100%);
  box-shadow: 0px -1px 55px -16px rgba(225, 81, 81, 0.78);
  -webkit-box-shadow: 0px -1px 55px -16px rgba(225, 81, 81, 0.78);
  -moz-box-shadow: 0px -1px 55px -16px rgba(225, 81, 81, 0.78);
}

.icon {
  max-width: 15px;
}
.icon-input {
  color: rgb(222, 226, 230);
  padding-left: 10px;
  min-width: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px rgb(222, 226, 230) solid;
  border-radius: 20px 0px 0px 20px !important;
  background-color: white;
}

input{
  border-left: none !important;
  border-radius: 0px 20px 20px 0px !important;
  font-size: 13px !important;
}

h1 {
  font-family: 'Alkatra';
  font-style: italic;
  margin-top: 3rem;
  color: white;
  text-shadow: 0px 0px 10px #9e0c0c70;
}

form {
  min-height: 200px;
  display: flex;
  justify-content: space-around;
  font-size: 13px;
}

button {
  border-radius: 20px;
  border: none;
  height: 38px;
  background-color: white;
  color: #FF5151;
  box-shadow: 0 0 10px 5px #9e0c0c33;
}