
table{
    width: 100%;
    margin-bottom: 2rem;
}

table tbody{
    background-color: #ffffff;
}

table th{
    background-color: rgba(255, 81, 81, 1);
    color: white;
    text-align: center;
    
}

table, td, th{
   
    padding: 0.5rem;
    text-align: left;
}

table tbody tr:hover td {
    background-color: #fdb8c1;
    
}
table button{
    box-shadow: none;
}
#delete{
    background-color: #980f21 ;
    color: white;
}
#update{
    color: white;
    background-color: #0f4a98;
}
